import React, { Component } from "react"
import { StaticMap } from "react-map-gl"
import { DeckGL } from "deck.gl"
import FlowMapLayer from "@flowmap.gl/core"

const MAPBOX_TOKEN = "pk.eyJ1IjoiZWRvcnl1bm9zdWtlIiwiYSI6ImNrbmhhZWs3ZjBrNWQyb3FoMjN5aXJxbmwifQ.Ul9kCWSG9Qj16gWXtH04lw"

const locations = [
{ id: 1, name: "My Home", lat: 40.811247, lon: 140.748359 },
{ id: 2, name: "HirosakiU Hospital", lat: 40.599558, lon: 140.465915 },
{ id: 3, name: "Syuncho Ryo", lat: 41.782201, lon: 140.800090 },
{ id: 4, name: "Towada", lat: 40.614048, lon: 141.209164 }
]
const flows = [
{ origin: 1, dest: 2, count: 42 },
{ origin: 2, dest: 1, count: 51 },
{ origin: 3, dest: 1, count: 50 },
{ origin: 2, dest: 3, count: 400 },
{ origin: 1, dest: 3, count: 22 },
{ origin: 3, dest: 2, count: 42 },
{ origin: 4, dest: 1, count: 200 }
]

export default class App extends Component {
  render () {
    return(
    <DeckGL
    controller={true}
    initialViewState={{ longitude: 0, latitude: 0, zoom: 1 }}
    layers={[
    new FlowMapLayer({
    id: "my-flowmap-layer",
    locations,
    flows,
    getLocationCentroid: (loc) => [loc.lon, loc.lat],
    onHover: (flow) => flow.count,
    }),
    ]}
    >
    <StaticMap mapboxApiAccessToken={MAPBOX_TOKEN} />
    </DeckGL>
    )
  }
}